import React from 'react';
import marble from '../../../img/header_marble.jpg';

function Tab_pop({ populations = [], players = 8 }) {
  const tab = () => {
    const row = [];

    let k = 0;

    const rows = () => {
      const cell = [];
      for (let j = 0; j < 2; j++) {
        cell.push(<td key={j} className={populations[k].selected ? 'strike' : ''}> {populations[k].name} </td>);
        k++;
      }
      return cell;
    };

    for (let i = 0; i < populations.length / 2; i++) {
      row.push(<tr key={i}>{rows()}</tr>);
    }
    return row;
  };

  return (
    <div className="col-md-3 p-0">
      <div className="rounded-top text-center pt-2 pb-2" style={{ backgroundImage: 'url(' + marble + ')' }}>
        <p className="text-white fw-bold mb-1 mt-1">
          Popolazioni - {players / 2} vs {players / 2}
        </p>
      </div>
      <table className="table table-bordered border border-2 border-dark mt-2 mb-0">
        <tbody id="testBody">{tab()}</tbody>
      </table>
    </div>
  );
}

export default Tab_pop;
