import React from 'react';

function history_argument({ code, date, teams }) {
  const formattedDate = new Date(date).toDateString();
  const win1 = teams[0]?.winner;
  const win2 = teams[1]?.winner;
  const players1 = teams[0]?.Players.map(({nickname}) => nickname).join(', ');
  const players2 = teams[1]?.Players.map(({nickname}) => nickname).join(', ');
  return (
    <tr>
      <td className="border-right" scope="row">
        {code}
      </td>
      <td className="border-right">{formattedDate}</td>
      <td>
        <i className={`fas ${win1 ? 'fa-crown' : ''}`} />
      </td>
      <td className="border-right">{players1}</td>
      <td>
        <i className={`fas ${win2 ? 'fa-crown' : ''}`} />
      </td>
      <td>{players2}</td>
    </tr>
  );
}

export default history_argument;
