import React, { useState, useRef } from 'react';
import axios from 'axios';
import archer from '../../../img/visual/maya.jpg';

const Toolbar = ({isLastGameFinished}) => {
  const [players, setPlayers] = useState(8);
  const [civ1, setCiv1] = useState('');
  const [civ2, setCiv2] = useState('');

  const modalRef = useRef();

  const handlePlayersSelect = (value) => {
    setPlayers(value);
  };

  const handleCiv1 = (value) => {
    setCiv1(value);
  };

  const handleCiv2 = (value) => {
    setCiv2(value);
  };

  const handleSubmit = async () => {
    try {
      await axios.post(
        `${process.env.REACT_APP_PROTOCOL}${process.env.REACT_APP_API_HOST}/game`,
        {
          civs: [civ1, civ2],
          players,
        },
        { withCredentials: true },
      );
      const modal = window.bootstrap.Modal.getInstance(modalRef.current);
      modal.hide();
    } catch (e) {
      console.log(e);
    }
  };

  return (
    <div className="row mb-4">
      <div className="col-md d-flex justify-content-end">
        <button
          type="button"
          className="btn btn-outline-dark border-0"
          data-bs-toggle="modal"
          data-bs-target="#newGame"
        >
          <i className="fas fa-plus mr-2" />
          <b>Nuova partita</b>
        </button>
      </div>

      <div
        className="modal fade"
        id="newGame"
        tabIndex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
        ref={modalRef}
      >
        <div className="modal-dialog modal-lg">
          <div className="modal-content">
            <div className="modal-header">
              <h4 className="modal-title" id="exampleModalLabel">
                <b>Nuova partita</b>
              </h4>
              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
            </div>
            <div className="modal-body">
              {!isLastGameFinished && (
                <div className="alert alert-danger mt-2 mb-5" role="alert">
                  I risultati della partita precedente <b>non sono stati registrati</b> e cliccando su "Crea nuova
                  partita" verranno persi.
                </div>
              )}
              <div className="row m-4 mb-0">
                <div className="col-md-8">
                  <div className="row">
                    <div className="col-md-6">
                      <h6>
                        <b>Popolazioni:</b>
                      </h6>
                      <div className="dropdown disabled pl-3">
                        <button
                          className="btn disabled dropdown-toggle"
                          type="button"
                          id="dropdownMenuButton"
                          data-bs-toggle="dropdown"
                          aria-expanded="false"
                        >
                          Tutte
                        </button>
                        <ul className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                          <li>
                            <a className="dropdown-item" href="#">
                              Lista1
                            </a>
                          </li>
                          <li>
                            <a className="dropdown-item" href="#">
                              Lista2
                            </a>
                          </li>
                          <li>
                            <a className="dropdown-item" href="#">
                              Lista3
                            </a>
                          </li>
                        </ul>
                      </div>
                      <h6 className="mt-4">
                        <b>Tipologia di mappa:</b>
                      </h6>
                      <div className="dropdown disabled pl-3">
                        <button
                          className="btn disabled dropdown-toggle"
                          type="button"
                          id="dropdownMenuButton"
                          data-bs-toggle="dropdown"
                          aria-expanded="false"
                        >
                          Casuale di terra
                        </button>
                        <ul className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                          <li>
                            <a className="dropdown-item" href="#">
                              Casuale
                            </a>
                          </li>
                          <li>
                            <a className="dropdown-item" href="#">
                              Casuale di terra
                            </a>
                          </li>
                          <li>
                            <a className="dropdown-item" href="#">
                              Casuale di mare
                            </a>
                          </li>
                        </ul>
                      </div>
                    </div>
                    <div className="col-md-6">
                      <h6>
                        <b>Numero giocatori:</b>
                      </h6>
                      <div className="form-check mb-2 pl-5">
                        <input
                          className="form-check-input"
                          type="radio"
                          name="squadre"
                          value="4"
                          id="flexRadioDefault6"
                          checked={players === 4}
                          onChange={() => handlePlayersSelect(4)}
                        />
                        <label className="form-check-label" htmlFor="flexRadioDefault6">
                          2 vs 2
                        </label>
                      </div>
                      <div className="form-check mb-2 pl-5">
                        <input
                          className="form-check-input"
                          type="radio"
                          name="squadre"
                          value="6"
                          id="flexRadioDefault7"
                          checked={players === 6}
                          onChange={() => handlePlayersSelect(6)}
                        />
                        <label className="form-check-label" htmlFor="flexRadioDefault7">
                          3 vs 3
                        </label>
                      </div>
                      <div className="form-check mb-2 pl-5">
                        <input
                          className="form-check-input"
                          type="radio"
                          name="squadre"
                          value="8"
                          id="flexRadioDefault8"
                          checked={players === 8}
                          onChange={() => handlePlayersSelect(8)}
                        />
                        <label className="form-check-label" htmlFor="flexRadioDefault8">
                          4 vs 4
                        </label>
                      </div>
                    </div>
                  </div>
                  <div className="row mt-4">
                    <div className="col-md-10 border rounded p-4">
                      <h6>
                        <b>2 popolazioni aggiuntive:</b>
                      </h6>
                      <div className="d-inline-flex">
                        <div className="dropdown pl-3">
                          <button
                            className="btn dropdown-toggle"
                            type="button"
                            id="dropdownMenuButton"
                            data-bs-toggle="dropdown"
                            aria-expanded="false"
                            style={{ minWidth: '100px' }}
                          >
                            Pop. 1:&#x000A0;
                            {civ1}
                          </button>
                          <ul className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Berberi')}>
                                Berberi
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Birmani')}>
                                Birmani
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Etiopi')}>
                                Etiopi
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Khmer')}>
                                Khmer
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Malesi')}>
                                Malesi
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Maliani')}>
                                Maliani
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Portoghesi')}>
                                Portoghesi
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Vietnamiti')}>
                                Vietnamiti
                              </a>
                            </li>
                          </ul>
                        </div>
                        <div className="dropdown pl-3">
                          <button
                            className="btn dropdown-toggle"
                            type="button"
                            id="dropdownMenuButton"
                            data-bs-toggle="dropdown"
                            aria-expanded="false"                            
                          >
                            Pop. 2: &#x000A0;
                            {civ2}
                          </button>
                          <ul className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Berberi')}>
                                Berberi
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Birmani')}>
                                Birmani
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Etiopi')}>
                                Etiopi
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Khmer')}>
                                Khmer
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Malesi')}>
                                Malesi
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Maliani')}>
                                Maliani
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Portoghesi')}>
                                Portoghesi
                              </a>
                            </li>
                            <li>
                              <a className="dropdown-item" onClick={() => handleCiv1('Vietnamiti')}>
                                Vietnamiti
                              </a>
                            </li>
                          </ul>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-md-4 d-flex align-items-end flex-row-reverse p-0">
                  <img src={archer} className="w-75" alt="Archer" />
                </div>
              </div>
            </div>
            <div className="modal-footer border-0">
              <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">
                Annulla
              </button>
              <button type="button" className="btn btn-primary" onClick={() => handleSubmit()}>
                Crea nuova partita
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Toolbar;
