import React, { useState, useEffect, useCallback } from 'react';
import axios from 'axios';
import Table_argument from '../table_argument/table_argument.js';

const Table_structure = ({ game, socket }) => {
  const [options, setOptions] = useState({});
  const [players, setPlayers] = useState([]);
  useEffect(() => {
    const getOptions = async () => {
      const response = await axios.get(`${process.env.REACT_APP_PROTOCOL}${process.env.REACT_APP_API_HOST}/teams`);
      setOptions(response.data);
    };

    getOptions();
  }, []);

  useEffect(() => {
    const getPlayers = async () => {
      const response = await axios.get(`${process.env.REACT_APP_PROTOCOL}${process.env.REACT_APP_API_HOST}/players`);
      setPlayers(response.data);
    };

    getPlayers();
  }, []);

  useEffect(() => {
    socket.on('teams', (data) => {
      setOptions(data);
    });
  }, []);

  const setTeamHandler = useCallback(async (teams, option) => {
    try {
      await axios.put(`${process.env.REACT_APP_PROTOCOL}${process.env.REACT_APP_API_HOST}/game/${game.id}/teams`, { teams, option });
    } catch (e) {
      console.log(e);
    }
  }, [game]);

  const clearTeamsHandler = useCallback(async (teams) => {
    try {
      await axios.put(`${process.env.REACT_APP_PROTOCOL}${process.env.REACT_APP_API_HOST}/team/${teams[0]}/clear`, {});
      await axios.put(`${process.env.REACT_APP_PROTOCOL}${process.env.REACT_APP_API_HOST}/team/${teams[1]}/clear`, {});
    } catch (e) {
      console.log(e);
    }
  }, [game]);

  return (
    <div className="row justify-content-evenly m-0 pt-5 pl-0 pr-0">
      {Object.values(options).map((teamOption, index) => (
        <Table_argument
          key={index}
          teams={teamOption}
          players={players}
          selected={game.option}
          setTeamHandler={setTeamHandler}
          clearTeamsHandler={clearTeamsHandler}
        />
      ))}
    </div>
  );
};

export default Table_structure;
