import React, { useEffect, useState } from 'react';
import axios from 'axios';
import History_argument from '../../argument/history/history_argument.js';

import marble from '../../../../img/header_marble.jpg';

const Tab_map = () => {
  const [history, setHistory] = useState([]);
  useEffect(async () => {
    try {
      const { data } = await axios.get(`${process.env.REACT_APP_PROTOCOL}${process.env.REACT_APP_API_HOST}/games`);
      setHistory(data);
    } catch (e) {
      console.log(e);
    }
  }, []);

  return (
    <div className="row">
      <div className="col-md-11 offset-md-1 mt-3">
        <div className="rounded-top text-center pt-2 pb-2" style={{ backgroundImage: 'url(' + marble + ')' }}>
          <p className="text-white fw-bold mb-1 mt-1">Storico</p>
        </div>
        <table className="table border border-2 border-dark mt-2 mb-0">
          <thead className="table-dark">
            <tr>
              <th scope="col">N°</th>
              <th scope="col">Data</th>
              <th scope="col"></th>
              <th scope="col">Team 1</th>
              <th scope="col"></th>
              <th scope="col">Team 2</th>
            </tr>
          </thead>
          <tbody id="match-history">
            {history.map(({createdAt, id, Teams}, index) => (
              <History_argument
                key={id}
                code={index + 1}
                date={createdAt}
                teams={Teams}
              />
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default Tab_map;
