import React, { useState, useEffect } from 'react';
import { Switch, Route, useHistory, Redirect } from 'react-router-dom';
import axios from 'axios';
import { UserContext } from './context';
import loginPage from './components/login/structure/login_structure.js';
import nicknamePage from './components/login/nickname/nickname.js';
import Home from './components/home';
import './css/bootstrap.css';
import './css/style.css';

function App() {
  const [loggedUser, setLoggedUser] = useState(null);
  const [lastGame, setLastGame] = useState({});
  const history = useHistory();

  useEffect(async () => {
    try {
      const {
        data: { user, game },
      } = await axios.get(`${process.env.REACT_APP_PROTOCOL}${process.env.REACT_APP_API_HOST}/game/last`, { withCredentials: true });
      setLoggedUser(user);
      setLastGame(game); //TODO move game
      history.push('/');
    } catch (e) {
      console.log(e);
      history.push('/login');
    }
  }, []);

  useEffect(() => {
    if (!!loggedUser && !loggedUser.nickname) {
      history.push('/nickname');
    }
  });

  return (
    <UserContext.Provider value={loggedUser}>
      <div className="container-fluid">
        <Switch>
          <Route exact path="/login" component={loginPage} />
          <Route exact path="/nickname" component={nicknamePage} />
          {loggedUser && (
            <Route path="/">
              <Home lastGame={lastGame} />
            </Route>
          )}
        </Switch>
      </div>
    </UserContext.Provider>
  );
}

export default App;
