import React, { useCallback, useEffect, useState } from 'react';
import axios from 'axios';
import { io } from 'socket.io-client';
import { includes, forEach, set } from 'lodash-es';

import Toolbar from '../toolbar/toolbar.js';
import Teams from '../teams/teams_structure/teams_structure.js';
import Tab_pop from '../tab_pop/tab_pop.js';
import Tab_map from '../tab_map/tab_map.js';
import Options_structure from '../options/table_structure/table_structure.js';

const socket = io(`${process.env.REACT_APP_WS}${process.env.REACT_APP_API_HOST}`);

const civsFromObj = (team) => [...Object.values(team.banned), ...Object.values(team.picked)];

const Page_picknbans = ({ lastGame }) => {
  let gameComponents;
  const [game, setGame] = useState(lastGame);

  useEffect(() => {
    socket.on('game', (data) => {
      setGame(data);
    });
    return () => {
      socket.off('game');
    };
  }, []);

  const onSelectHandler = useCallback(
    async (population, team, type, pos) => {
      const { picks } = game; //TODO clone
      picks[team][type][pos] = population;
      try {
        await axios.put(`${process.env.REACT_APP_PROTOCOL}${process.env.REACT_APP_API_HOST}/game/${game.id}`, {
          picks,
        });
      } catch (e) {
        console.log(e);
      }
    },
    [game],
  );

  if (game && game.id) {
    const { civs, picks } = game;
    const unavailableCivs = [...civsFromObj(picks.Team1), ...civsFromObj(picks.Team2)];
    forEach(civs, (civ) => set(civ, 'selected', includes(unavailableCivs, civ.name)));
    const players = picks?.players;
    gameComponents = (
      <>
        <div className="row justify-content-evenly overflow-auto pnb-scroll m-0 p-0 mt-5" id="pnb">
          <Teams teams={picks} players={players} populations={civs} onSelect={onSelectHandler} />
          <Tab_pop populations={civs} players={players} unavailable={unavailableCivs} />
          <Tab_map game={game} />
          <Options_structure game={game} socket={socket} />
        </div>
      </>
    );
  }

  return (
    <div>
      <Toolbar isLastGameFinished={game?.end} />
      {game && game.id && gameComponents}
    </div>
  );
};

export default Page_picknbans;
